<template>
  <div class="card">
    <div class="header">
      <h4 class="title">Edit Profile</h4>
    </div>
    <div class="content">
      <form>
        <div class="row">
          <div class="col-md-5">
            <fg-input type="text" label="Company" :disabled="true" placeholder="Paper dashboard" v-model="user.company" />
          </div>
          <div class="col-md-3">

            <fg-input type="text" label="Username" placeholder="Username" v-model="user.username" />
          </div>
          <div class="col-md-4">
            <fg-input type="email" label="Username" placeholder="Email" v-model="user.email" />
          </div>
        </div>

        <div class="row">
          <div class="col-md-6">
            <fg-input type="text" label="First Name" placeholder="First Name" v-model="user.firstName" />
          </div>
          <div class="col-md-6">
            <fg-input type="text" label="Last Name" placeholder="Last Name" v-model="user.lastName" />
          </div>
        </div>

        <div class="row">
          <div class="col-md-12">
            <fg-input type="text" label="Address" placeholder="Home Address" v-model="user.address" />
          </div>
        </div>

        <div class="row">
          <div class="col-md-4">
            <fg-input type="text" label="City" placeholder="City" v-model="user.city" />
          </div>
          <div class="col-md-4">
            <fg-input type="text" label="Country" placeholder="Country" v-model="user.country" />
          </div>
          <div class="col-md-4">
            <fg-input type="number" label="Postal Code" placeholder="ZIP Code" v-model="user.postalCode" />
          </div>
        </div>

        <div class="row">
          <div class="col-md-12">
            <div class="form-group">
              <label>About Me</label>
              <textarea rows="5" class="form-control border-input" placeholder="Here can be your description" v-model="user.aboutMe" />
            </div>
          </div>
        </div>
        <div class="text-center">
          <button type="submit" class="btn btn-info btn-fill btn-wd" @click.prevent="updateProfile">
            Update Profile
          </button>
        </div>
        <div class="clearfix" />
      </form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      user: {
        company: 'Paper Dashboard',
        username: 'michael23',
        email: '',
        lastName: 'Faker',
        address: 'Melbourne, Australia',
        city: 'melbourne',
        postalCode: '',
        aboutMe: 'Oh so, your weak rhyme. You doubt I\'ll bother, reading into it.I\'ll probably won\'t, left to my own devicesBut that\'s the difference in our opinions.',
      },
    };
  },
  methods: {
    updateProfile() {
      console.log(`Your data: ${JSON.stringify(this.user)}`);
    },
  },
};
</script>
<style>

</style>



