<template>
  <div>
    <back-button/>
    <div class="row">
      <div class="col-md-5">

        <h3>Números da {{comunidade.nome}}</h3>

        <value-box grid="col-md-12" color-class="bg-green" icon-class="fa fa-bank" :value="credits" text="Total de Receitas">
        </value-box>

        <value-box grid="col-md-12" color-class="bg-red" icon-class="fa fa-credit-card" :value="debts" text="Total de Gastos">
        </value-box>

        <value-box grid="col-md-12" color-class="bg-blue" icon-class="fa fa-money" :value="credits-debts" text="Valor Consolidado">
        </value-box>
      </div>

      <div class="col-md-7">
        <update-form :comunidade="comunidade" />
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios';
import UpdateForm from './UpdateForm';
import {
  comunidadesApiUrl,
  findBillingCycleByComunityId,
} from './../../../../api-url';
import ValueBox from '../../../UIComponents/ValueBox';

export default {
  components: {
    UpdateForm,
    ValueBox,
  },
  data() {
    return {
      comunidade: {},
      billingCycles: [],
    };
  },
  mounted() {
    const self = this;
    axios
      .get(`${comunidadesApiUrl}/${self.$route.params.id}`)
      .then(({ data }) => {
        this.comunidade = data;
      })
      .then(() => {
        axios
          .get(findBillingCycleByComunityId(this.comunidade.id))
          .then(({ data }) => {
            this.billingCycles = data;
          });
      })
      .catch(err => console.log(err));
  },
  computed: {
    debts() {
      return this.reduceCreditOrDebt('debts');
    },
    credits() {
      return this.reduceCreditOrDebt('credits');
    },
  },
  methods: {
    reduceCreditOrDebt(creditOrDebt) {
      return this.billingCycles.reduce((prev, curr) => prev + curr[creditOrDebt].reduce((prevDebtOrCredit, currDebtOrCredit) => prevDebtOrCredit + currDebtOrCredit.value, 0), 0);
    },
  },
};
</script>

<style lang="sass">

</style>



